<template>
  <div>
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button icon="el-icon-plus" size="small" @click="handleAddEdit('添加网关实例')" type="warning">{{$t('common.add')}}</el-button>
      <div class="inline-block ml-10" v-if="tableVal.length !== 0">
        <el-button  type="primary" size="small" @click="handleInstall()" icon="fa fa-plane">{{$t('common.deploy')}}</el-button>
        <el-button  type="danger" size="small" @click="handleUninstall()" icon="fa fa-link">{{$t('common.unload')}}</el-button>
        <el-button  type="primary" size="small" @click="handleStart()" icon="fa fa-play">{{$t('common.start')}}</el-button>
        <el-button  type="danger" size="small" @click="handleStop()" icon="fa fa-stop">{{$t('common.stop')}}</el-button>
        <el-button  type="danger" size="small" @click="deleteRow()" icon="el-icon-delete">{{$t('common.delete')}}</el-button>
      </div>
    </div>
    <el-form ref="listForm" :model="listForm" :rules="inputRules" label-position='right' status-icon label-width="86px" size="small">
      <el-row class="label-first" :gutter='20'>
        <el-col :xl="4" :lg="6">
            <el-form-item :label="$t('coreApp.tableList.nodeName')+'：'">
              <el-input type="text" v-model.trim="coreForm.name"  maxlength="100" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :xl="4" :lg="6">
            <el-form-item :label="$t('coreApp.tableList.ip')+'：'">
              <el-autocomplete
                class="inline-input"
                placeholder="请输入1-15位字符"
                maxlength="15"
                v-model.trim="coreForm.ip"
                :fetch-suggestions="ipSearch"
              ></el-autocomplete>
            </el-form-item>
          </el-col>
          <el-col :xl="4" :lg="6">
            <el-button size="small" type="primary" icon="el-icon-search" @click="search('search')">{{$t('common.search')}}</el-button>
          </el-col>
      </el-row>
    </el-form>
    <el-table
      stripe
      v-loading="loading"
      :data="tableList"
      @selection-change="handleSelectionChange"
      style="width: 100%">
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column
        align="center"
        prop="name"
        :label="$t('coreApp.tableList.nodeName')"
        width="120">
      </el-table-column>
      <el-table-column
        align="center"
        prop="name"
        :label="$t('service.provider.status')"
        width="135">
        <template slot-scope="props">
          <span :style="props.row.status | filterAppStatusColor"><i v-if="props.row.status !=='UNINSTALL'" class="fa fa-lightbulb-o name-state"></i>{{props.row.status | filterAppStatus}}</span>
          <el-button type="text" size="small" @click="refresh(props.row, 'search')" icon="fa fa-refresh"></el-button>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="ip"
        :label="$t('coreApp.tableList.ip')"
        width="130">
      </el-table-column>
      <el-table-column
        align="center"
        prop="installName"
        :label="$t('coreApp.tableList.installName')"
        width="150">
      </el-table-column>
      <el-table-column
        align="center"
        prop="installPath"
        :label="$t('coreApp.tableList.path')"
        width="160">
      </el-table-column>
      <el-table-column
        align="center"
        prop="versionNo"
        :label="$t('coreApp.tableList.versionNum')"
        width="120">
      </el-table-column>
      <el-table-column
        align="center"
        prop="remark"
        :label="$t('coreApp.tableVersion.desc')" show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        :label="$t('common.operation')"
        fixed="right"
        width="90"
        align="center">
        <template slot-scope="scope">
          <el-dropdown trigger="click">
            <el-button type="text" size="mini">
              操作<i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>
                <el-button @click="handleAddEdit('修改网关实例',scope.row)" type="text" size="mini" icon="el-icon-edit">{{$t('common.edit')}}</el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button v-if="scope.row.status === 'UNINSTALL'"  @click="deleteRow(scope.row)" type="text"  size="mini" icon="el-icon-delete" class="btn-text-red">{{$t('common.delete')}}</el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button v-if="scope.row.status === 'UNINSTALL'"  @click="handleInstall(scope.row)" type="text" icon="fa fa-plane"  size="mini">{{$t('common.deploy')}}</el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button v-if="scope.row.status === 'INSTALL' || scope.row.status === 'STOPPED'"  @click="handleUninstall(scope.row)" icon="fa fa-link" type="text"  size="mini">{{$t('common.unload')}}</el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button v-if="scope.row.status === 'INSTALL' || scope.row.status === 'STOPPED'"  @click="handleStart(scope.row)" icon="fa fa-play" type="text"  size="mini">{{$t('common.start')}}</el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button v-if="scope.row.status === 'RUNNING'"  @click="handleStop(scope.row)" type="text" icon="fa fa-stop"  size="mini" class="btn-text-red">{{$t('common.stop')}}</el-button>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>
    <!-- 翻页部分 -->
    <div class="block">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page="pageNo"
        :page-size="10"
        layout="total, prev, pager, next, jumper"
        background
        :total="total">
      </el-pagination>
    </div>
    <!-- 弹出框 -->
    <el-dialog :title="listTitle" @close="cancel" :visible.sync="addVisible" width="40%" center>
      <el-form ref="listForm" :model="listForm" :rules="inputRules" status-icon label-width="100px" size="small">
        <el-form-item v-if="listTitle === '添加网关实例'">
          <el-row>
            <el-col :span="24">
              <el-radio-group v-model="listForm.status" @change="radioChange">
                <el-radio label="UNINSTALL">未部署</el-radio>
                <el-radio label="INSTALL">已部署</el-radio>
              </el-radio-group>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item :label="$t('coreApp.tableList.nodeName')" prop="name">
          <el-row>
            <el-col :span="24">
              <el-input v-model.trim="listForm.name" type="text" placeholder="请输入1-50位字符" maxlength="50" clearable></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item :label="$t('coreApp.tableList.installName')" prop="installName">
          <el-row>
            <el-col :span="24">
              <el-input v-model.trim="listForm.installName" :disabled="isStatus" type="text" placeholder="请输入1-100位字符" maxlength="100" clearable></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item :label="$t('coreApp.tableList.path')" prop="installPath">
          <el-row>
            <el-col :span="24">
              <el-input v-model.trim="listForm.installPath" :disabled="isStatus" clearable type="text" placeholder="请输入1-200位字符" maxlength="200"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item prop="ip" :label="$t('coreApp.tableList.ip')">
          <el-row>
            <el-col :span="24">
              <el-autocomplete
                class="inline-input"
                placeholder="请输入1-15位字符"
                maxlength="15"
                v-model.trim="listForm.ip"
                :fetch-suggestions="ipSearch"
                :disabled="isStatus"
                style="width:100%"
              ></el-autocomplete>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item prop="versionNo" :label="$t('coreApp.tableList.versionNum')">
          <el-row>
            <el-col :span="24">
              <el-autocomplete
                class="inline-input"
                placeholder="请输入1-15位字符"
                maxlength="15"
                v-model.trim="listForm.versionNo"
                :fetch-suggestions="versionSearch"
                :disabled="isStatus"
                style="width:100%"
              ></el-autocomplete>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item :label="$t('coreApp.tableVersion.desc')">
          <el-row>
            <el-col :span="24">
              <el-input type="textarea" v-model.trim="listForm.remark" placeholder="请输入1-100位字符" maxlength="100"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>
      <sub-template :handle-flag="true"></sub-template>
      <span slot="footer" class="dialog-footer">
          <el-button @click="saveAddEdit" :loading="loading" type="primary" size="small">{{$t('common.ok')}}</el-button>
          <el-button @click="cancel()" size="small">{{$t('common.cancel')}}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import Api from '@/constants/Api.js'

  export default {
    data() {
      return {
        pageNo: 1, // 当前页数
        limit: 5, // 每页条数
        total: 0, // 总页数
        versionOptions: [],
        ipOptions: [],
        // 查询
        coreForm: {},
        tableList: [], // table原始数据
        idx: -1,
        addVisible: false,
        radio: '1',
        ifShow: false,
        // 查询规则
        inputRules: {
          name: [
            { required: true, message: '请输入版本名称' }
          ],
          installName: [
            { required: true, message: '请输入安装名' }
          ],
          installPath: [
            { required: true, message: '请输入安装路径' }
          ],
          ip: [
            { required: true, message: '请输入Ip' }
          ],
          versionNo: [
            { required: true, message: '请输入版本号' }
          ]
        },
        loading: false,
        // 模板
        listForm: {},
        listTitle: '',
        isStatus: false, // 是否能输入
        tableVal: []
      }
    },
    computed: {
    },
    created() {
      this.getData()
    },
    methods: {
      getData() { // 初始化数据
        this.$axios.all([
            this.$axios.post(Api.coreVersionNoCombox, {'moType': 'Agent'}),
            this.$axios.post(Api.hostSearchIpList, {'moType': 'Agent'})
          ])
          .then(this.$axios.spread((versionData, hostData) => {
            // 下拉版本
            let versionOs = versionData.data.map((item) => {
              return {
                value: item.pkValue,
                label: item.pkDesc
              }
            })
            this.versionOptions = versionOs
            // 下拉Ip
            let hostIp = hostData.data.map((item) => {
              return {
                value: item.pkValue,
                label: item.pkDesc
              }
            })
            this.ipOptions = hostIp
          }))
          this.pageNo = 1
          this.limit = 5
          this.search()
      },
      // 翻页事件
      handleCurrentChange(val) {
        this.pageNo = val
        this.search()
      },
      search(title) {
        this.loading = true
        let {name, ip} = this.coreForm
        // name.replace(/\s/g, '')
        // ip.replace(/\s/g, '')
        let query = Object.assign({}, {
          'pageNo': this.pageNo,
          'limit': this.limit,
          'baseParam': {
            'moType': 'Agent', name, ip
          }
        })
        this.$axios.post(Api.coreNodeFind, query)
          .then((searchData) => {
            if (title === 'search') {
              this.$message.success(`查询成功`)
            }
            let data = JSON.parse(JSON.stringify(searchData.data))
            this.tableList = data.data
            this.refresh(data.data)
            this.total = data.recordsTotal
            this.loading = false
          }).catch((error) => {
            this.loading = false
            this.$message.error(error.data.message || error.message || error)
          })
      },
      // 刷新
      refresh(rowVal, title) {
        let rowValData = JSON.parse(JSON.stringify(rowVal))
        let params
        let query = []
        if (Array.isArray(rowVal)) {
          params = JSON.parse(JSON.stringify(rowVal))
          rowValData.forEach(({deployId}) => {
            query.push({deployId})
          })
        } else {
          params = JSON.parse(JSON.stringify(this.tableList))
          let {deployId} = rowValData
          query.push({deployId})
        }
        this.loading = true
        this.$axios.post(Api.coreAppStatus, query).then((res) => {
          if (res.code === '0000') {
            params.map((value) => {
              res.data.forEach((item) => {
                if (item.deployId === value.deployId) {
                  value.status = item.status
                  return value
                } else {
                  return value
                }
              })
            })
            this.loading = false
            this.tableList = params
            if (title === 'search') {
              this.$message.success(`刷新成功`)
            }
          }
        }).catch((error) => {
          this.loading = false
          this.$message.error(error.data.message || error.message || error)
        })
      },
      // 新增/编辑
      handleAddEdit(title, value) {
        if (title === '添加网关实例') {
          this.listTitle = title
          this.isStatus = false
          this.listForm = {
            status: 'UNINSTALL'
          }
        }
        if (title === '修改网关实例') {
          this.listTitle = title
          if (value.status === 'UNINSTALL') {
            this.isStatus = false
          } else {
            this.isStatus = true
          }
          this.listForm = JSON.parse(JSON.stringify(value))
        }
        this.addVisible = true
      },
      saveAddEdit() {
        let {deployId, name, ip, installName, installPath, versionNo, remark, status} = JSON.parse(JSON.stringify(this.listForm))
        let query = Object.assign({}, {deployId, name, ip, installName, installPath, versionNo, remark, status})
        query.moType = 'Agent'
        if (this.listTitle === '添加网关实例') {
          delete query.deployId
          if (!name || !ip || !installName || !installPath || !versionNo || !status) {
            this.$message({
              message: '带*值不能为空',
              type: 'warning'
            })
            return false
          }
          this.$axios.post(Api.coreNodeAdd, query).then((res) => {
            if (res.code === '0000') {
              this.search()
              this.$message.success(`新增成功`)
              this.addVisible = false
            }
          })
        }
        if (this.listTitle === '修改网关实例') {
          delete query.status
          if (!name || !ip || !installName || !installPath || !versionNo) {
            this.$message({
              message: '带*值不能为空',
              type: 'warning'
            })
            return false
          }
          this.loading = true
          this.$axios.post(Api.coreNodeModify, query).then((res) => {
            if (res.code === '0000') {
              this.search()
              this.$message.success(`编辑成功`)
              this.addVisible = false
            }
          }).catch((error) => {
            this.loading = false
            this.$message.error(error.data.message || error.message || error)
          })
        }
      },
      cancel() {
        this.addVisible = false
        this.$refs.listForm.resetFields()
      },
      // 部署节点
      handleInstall(value) {
        this.conmmonMethods.makePopBox(this, '部署节点').then(() => {
          let query
          let url
          let errorName = ''
          if (value) {
            query = {}
            let {deployId, moType, ip, installName, installPath, versionNo} = JSON.parse(JSON.stringify(value))
            query = Object.assign({}, {deployId, moType, ip, installName, installPath, versionNo})
            url = Api.coreAppInstall
          } else {
            if (Array.isArray(this.tableVal)) {
              query = []
              url = Api.coreAppBatchInstall
              this.tableVal.forEach((value) => {
                let {name, deployId, moType, ip, installName, installPath, versionNo, status} = value
                if (status === 'UNINSTALL') {
                  query.push({deployId, moType, ip, installName, installPath, versionNo})
                } else {
                  errorName += name + '、'
                }
              })
            }
          }
          if (errorName.length > 0) {
            this.$message({
              message: '节点名称：' + errorName.slice(0, errorName.length - 1) + '不符合该操作，请重新选择',
              type: 'warning'
            })
            return false
          }
          this.loading = true
          this.$axios.post(url, query).then((res) => {
            if (res.code === '0000') {
              this.$message({
                message: '部署成功',
                type: 'success'
              })
              this.search()
            }
          }).catch((error) => {
            this.loading = false
            this.$message.error(error.data.message || error.message || error)
          })
        })
      },
      // 卸载节点
      handleUninstall(value) {
        this.conmmonMethods.makePopBox(this, '卸载节点').then(() => {
          let query
          let url
          let errorName = ''
          if (value) {
            query = {}
            let {deployId, ip, installName, installPath, versionNo, moType} = JSON.parse(JSON.stringify(value))
            query = Object.assign({}, {deployId, ip, installName, installPath, versionNo, moType})
            url = Api.coreAppUninstall
          } else {
            if (Array.isArray(this.tableVal)) {
              query = []
              url = Api.coreAppBatchUninstall
              this.tableVal.forEach((value) => {
                let {name, deployId, ip, installName, installPath, status, versionNo, moType} = value
                if (status === 'INSTALL' || status === 'STOPPED') {
                  query.push({deployId, ip, installName, installPath, versionNo, moType})
                } else {
                  errorName += name + '、'
                }
              })
            }
          }
          if (errorName.length > 0) {
            this.$message({
              message: '节点名称：' + errorName.slice(0, errorName.length - 1) + '不符合该操作，请重新选择',
              type: 'warning'
            })
            return false
          }
          this.loading = true
          this.$axios.post(url, query).then((res) => {
            if (res.code === '0000') {
              this.$message({
                message: '卸载成功',
                type: 'success'
              })
              this.search()
            }
          }).catch((error) => {
            this.loading = false
            this.$message.error(error.data.message || error.message || error)
          })
        })
      },
      // 启动节点
      handleStart(value) {
        this.conmmonMethods.makePopBox(this, '启动节点').then(() => {
          let query
          let url
          let errorName = ''
          if (value) {
            query = {}
            let {moType, ip, installName, installPath} = JSON.parse(JSON.stringify(value))
            query = Object.assign({}, {moType, ip, installName, installPath})
            url = Api.coreAppStart
          } else {
            if (Array.isArray(this.tableVal)) {
              query = []
              url = Api.coreAppBatchStart
              this.tableVal.forEach((value) => {
                let {moType, name, ip, installName, installPath, status} = value
                if (status === 'INSTALL' || status === 'STOPPED') {
                  query.push({moType, ip, installName, installPath})
                } else {
                  errorName += name + '、'
                }
              })
            }
          }
          if (errorName.length > 0) {
            this.$message({
              message: '节点名称：' + errorName.slice(0, errorName.length - 1) + '不符合该操作，请重新选择',
              type: 'warning'
            })
            return false
          }
          this.loading = true
          this.$axios.post(url, query).then((res) => {
            if (res.code === '0000') {
              this.$message({
                message: '启动成功',
                type: 'success'
              })
              this.search()
            }
          }).catch((error) => {
            this.loading = false
            this.$message.error(error.data.message || error.message || error)
          })
        })
      },
      // 停止节点
      handleStop(value) {
        this.conmmonMethods.makePopBox(this, '停止节点').then(() => {
          let query
          let url
          let errorName = ''
          if (value) {
            let {moType, ip, installName, installPath} = JSON.parse(JSON.stringify(value))
            query = Object.assign({}, {moType, ip, installName, installPath})
            url = Api.coreAppStop
          } else {
            if (Array.isArray(this.tableVal)) {
              query = []
              url = Api.coreAppBatchStop
              this.tableVal.forEach((value) => {
                let {moType, name, ip, installName, installPath, status} = value
                if (status === 'RUNNING') {
                  query.push({moType, ip, installName, installPath})
                } else {
                  errorName += name + '、'
                }
              })
            }
          }
          if (errorName.length > 0) {
            this.$message({
              message: '节点名称：' + errorName.slice(0, errorName.length - 1) + '不符合该操作，请重新选择',
              type: 'warning'
            })
            return false
          }
          this.loading = true
          this.$axios.post(url, query).then((res) => {
            if (res.code === '0000') {
              this.$message({
                message: '停止成功',
                type: 'success'
              })
              this.search()
            }
          }).catch((error) => {
            this.loading = false
            this.$message.error(error.data.message || error.message || error)
          })
        })
      },
      // 删除
      deleteRow(value = null) {
        this.conmmonMethods.makePopBox(this, '删除').then(() => {
          if (value) {
            let {deployId, status} = value
            if (status === 'UNINSTALL') {
              this.loading = true
              this.$axios.delete(Api.coreNodeDelete + deployId).then((res) => {
                if (res.code === '0000') {
                  this.search()
                  this.$message.success(`删除成功`)
                }
              }).catch((error) => {
                this.loading = false
                this.$message.error(error.data.message || error.message || error)
              })
            } else {
              this.$message({
                message: '未部署的节点才能被删除',
                type: 'warning'
              })
            }
          } else {
            let query = []
            let errorName = ''
            this.tableVal.forEach((value) => {
              let {name, deployId, status} = value
              if (status === 'UNINSTALL') {
                query.push({deployId})
              } else {
                errorName += name + '、'
              }
            })
            if (errorName.length > 0) {
              this.$message({
                message: '节点名称：' + errorName.slice(0, errorName.length - 1) + '不符合该操作，请重新选择',
                type: 'warning'
              })
              return false
            }
            this.loading = true
            this.$axios.post(Api.coreNodeBatchDelete, query).then((res) => {
              this.$message.success(`删除成功`)
              this.search()
            }).catch((error) => {
              this.loading = false
              this.$message.error(error.data.message || error.message || error)
            })
          }
        })
      },
      // 全选
      handleSelectionChange(val) {
        this.tableVal = val
      },
      // 部署选择
      radioChange(val) {
        this.listForm.status = val
      },
      // 下拉选择
      ipSearch(value, cb) {
        let query = this.ipOptions
        let results = value ? query.filter((item) => {
          return (item.value.toLowerCase().indexOf(value.toLowerCase()) === 0)
          }) : query
        cb(results)
      },
      // 下拉选择
      versionSearch(value, cb) {
        var query = this.versionOptions
        var results = value ? query.filter((item) => {
          return (item.value.toLowerCase().indexOf(value.toLowerCase()) === 0)
          }) : query
        cb(results)
      }
    }
  }
</script>

<style lang="scss" scoped>
  .name-state {
    margin-right: 10px;
    font-size: 18px;
    font-weight: bolder;
  }
</style>



